#sliderImage{
    margin-top: 64px;
    width: 100%;
  }
  .slider{
      width: 1600px;
      max-width: 99%;
      height: 500px;
      margin: auto;
      position: relative;
      overflow: hidden;
      border-radius: 0.5rem;
  }
  .slider .list{
      position: absolute;
      width: max-content;
      height: 100%;
      left: 0;
      top: 0;
      display: flex;
      transition: 1s;
  }
  .slider .list img{
      width: 1600px;
      max-width: 100vw;
      height: 100%;
      object-fit: cover;
  }
  .slider .buttons{
      position: absolute;
      top: 45%;
      left: 5%;
      width: 90%;
      display: flex;
      justify-content: space-between;
  }
  .slider .buttons button{
      width: 45px;
      height: 60px;
      border-radius: 4%;
      background-color: #fff5;
      color: #fff;
      border: none;
      font-family: monospace;
      font-weight: bold;
      opacity: 0.4;
  }
  .slider .buttons button:hover{
    opacity: 1;
  }
  .slider .dots{
      position: absolute;
      bottom: 10px;
      left: 0;
      color: #fff;
      width: 100%;
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center;
  }
  .slider .dots li{
      list-style: none;
      width: 10px;
      height: 10px;
      background-color: #fff;
      margin: 10px;
      border-radius: 20px;
      transition: 0.5s;
  }
  .slider .dots li.active{
      width: 30px;
  }
  @media print, screen and (max-width: 768px) {
    .slider {
        height: 220px;
    }
}